$primary-color : #de9d22;
$blue-color : #0d52a7;
$green-color : #208b73;
$red-color : #d1452e;
$gray-color : #dbdbdb;
$deep-gray-color:#444444;

@mixin footer_select {
    width: 130px;
    padding: 8px;
    color: #fff;
    border: 1px solid #fff;
    background: none;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

// 헤더
#header {
    position: relative;
    width: 100%;
    z-index: 3;
    &.border {
        border-bottom:1px solid #eaeaea;
    }
    #gnb {
        width: 1280px;
        margin: 0 auto;
        display: flex;
        padding: 15px 0;
        // 로고
        h1 {
            display: inline-block;
            margin-right: 30px;
        }
        ul {
            display: inline-block;
            vertical-align: top;
            margin-left: auto;
            li {
                display: inline-block;
                vertical-align: middle;
                font-weight: bold;
                font-size:15px;
            }
        }
        .gnb_menu {
            li {
                &:nth-child(2) {
                    margin: 0 35px;
                }
            }
        }
        .gnb_left {
            ul {
                li {
                    margin-right:40px;
                }
            }
        }
        .gnb_right {
            float: right;
            li {
                color:#666666;
                margin-right:8px;
                cursor: pointer;
                &:first-child {
                    margin-right:15px;
                }
                &:last-child {
                    margin-right:0px;
                }
                a {
                    padding:5px 15px; 
                    border-radius: 20px;
                    font-size:14px;
                    span {
                        vertical-align: top;
                    }
                }
            }
        }
    }
}

// 필터 옵션
.box_component_wrap {
    border-bottom: 1px solid #eaeaea;
    z-index: 3;
    .box_component {
        width:1280px;
        margin:0 auto;
        padding:18px 0;
        ul {
            display: inline-block;
            li {
                display:inline-block;
                margin-right:10px;
                &:last-child {
                    margin-right:0;
                }
                button {
                    padding:10px 20px;
                    color:#666666;
                    border:1px solid #eaeaea;
                    border-radius:30px;
                    img {
                        margin-right:5px;
                    }
                }
            }
        }
        // 검색창 기능 [s]
        .main_sb_search{

            .sb-search{
                position: absolute;
                top: 63px;
                right: 16.4%;
                width: 0%;
                min-width: 60px;
                height: 78px;
                float: right;
                overflow: hidden;
                -webkit-transition: width 0.3s;
                -moz-transition: width 0.3s;
                transition: width 0.3s;
                -webkit-backface-visibility: hidden;
                &.sb-search-open {
                    position: absolute;
                    top: 63px;
                    right: 16.4%;
                    width: 21px;
                    width:23%;
                    border-right: 1px solid #eaeaea;
                    border-left: 1px solid #eaeaea;
                }
                form {
                    .sb-search-input {
                        position: absolute;
                        top: 0;
                        right: 0px;
                        border: none;
                        outline: none;
                        background: #fff;
                        width: 77%;
                        height: 36px;
                        margin: 0;
                        z-index: 10;
                        padding: 20px 65px 20px 20px;
                        font-family: inherit;
                        font-size: 20px;
                        color: #2c3e50;
                        border:none;
                    }
                    input[type="search"].sb-search-input {
                        -webkit-appearance: none;
                        -webkit-border-radius: 0px;
                    }
                    .sb-search-input::-webkit-input-placeholder {
                        color: #efb480;
                    }
                    .sb-search-input:-moz-placeholder {
                        color: #efb480;
                    }
                    .sb-search-input::-moz-placeholder {
                        color: #efb480;
                    }
            
                    .sb-search-input:-ms-input-placeholder {
                        color: #efb480;
                    }
                    .sb-icon-search,
                    .sb-search-submit {
                    width: 60px;
                    height: 78px;
                    display: block;
                    position: absolute;
                    right: 0;
                    top: 0;
                    padding: 0;
                    margin: 0;
                    line-height: 60px;
                    text-align: center;
                    cursor: pointer;
                    }
                    .sb-search-submit {
                        background: #fff;
                        /* IE needs this */
                        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
                        /* IE 8 */
                        filter: alpha(opacity=0);
                        /* IE 5-7 */
                        opacity: 0;
                        color: transparent;
                        color: red;
                        border: none;
                        outline: none;
                        z-index: -1;
                    }
                    .sb-icon-search {
                        color: #000;
                        background:  #fff;
                        z-index: 90;
                        font-size: 22px;
                        font-family: 'icomoon';
                        speak: none;
                        font-style: normal;
                        font-weight: normal;
                        font-variant: normal;
                        text-transform: none;
                        -webkit-font-smoothing: antialiased;
                        background: url('/images/search_cancle_btn.png') no-repeat;
                        background-position: center;
                    }
                    .sb-icon-search:before {
                        content: "";
                    }
                    .sb-search.sb-search-open .sb-icon-search,
                    .no-js .sb-search .sb-icon-search {
                    background: #da6d0d;
                    color: #fff;
                    z-index: 11;
                    }
                    input{
                        &:first-child{
                            &::placeholder{
                                color: #b0b0b0;
                                font-size: 20px;
                            }
                        }
                    }
                }
            }
        }
        // 검색창 기능 [e]
    }  
}

